.container {
    width: 100%;
    max-width: var(--container);
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    @media (min-width: 1300px) {
        padding-right: calc(var(--flex-gap-md)/2);
        padding-left: calc(var(--flex-gap-md)/2);
    }
}

.container-fluid {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    @media (min-width: 1300px) {
        padding-right: calc(var(--flex-gap-md)/2);
        padding-left: calc(var(--flex-gap-md)/2);
    }
}
.clearfix::after {
    display: block;
    clear: both;
    content: ""
}

.none {
    display: none
}

.inline {
    display: inline
}

.inline-block {
    display: inline-block
}

.block {
    display: block
}

.flex {
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    flex-wrap: wrap;
}

.inline-flex {
    display: -ms-inline-flexbox;
    display: -webkit-inline-box;
    display: inline-flex;
}

.grid {
    display: grid;
}

.flex-reverse {
    flex-direction: row-reverse;
}

@media (min-width:750px) {
    .container {
        max-width: var(--container-md);
    }
    .md-none {
        display: none;
    }
    .md-inline {
        display: inline
    }
    .md-inline-block {
        display: inline-block
    }
    .md-block {
        display: block
    }
    .md-flex {
        display: -ms-flexbox;
        display: -webkit-box;
        display: flex
    }
    .md-inline-flex {
        display: -ms-inline-flexbox;
        display: -webkit-inline-box;
        display: inline-flex
    }
}

@media (min-width:1000px) {
    .container {
        max-width: var(--container-lg);
    }
    .lg-none {
        display: none
    }
    .lg-inline {
        display: inline
    }
    .lg-inline-block {
        display: inline-block
    }
    .lg-block {
        display: block
    }
    .lg-flex {
        display: -ms-flexbox;
        display: -webkit-box;
        display: flex
    }
    .lg-inline-flex {
        display: -ms-inline-flexbox;
        display: -webkit-inline-box;
        display: inline-flex
    }
}

@media (min-width:1300px) {
    .container {
        max-width: var(--container-xl);
    }
    .xl-none {
        display: none
    }
    .xl-inline {
        display: inline
    }
    .xl-inline-block {
        display: inline-block
    }
    .xl-block {
        display: block
    }
    .xl-flex {
        display: -ms-flexbox;
        display: -webkit-box;
        display: flex
    }
    .xl-inline-flex {
        display: -ms-inline-flexbox;
        display: -webkit-inline-box;
        display: inline-flex
    }
}


@for $index from 1 through 12 {
    .flex-#{$index} >* {
        flex: 0 0 calc(100% / #{$index});
        max-width: calc(100% / #{$index});
    }
}

@media (min-width: 750px) {
    @for $index from 1 through 12 {
        .flex-md-#{$index} >* {
          flex: 0 0 calc(100% / #{$index});
          max-width: calc(100% / #{$index});
        }
    }
}

@media (min-width: 1000px) {
    @for $index from 1 through 12 {
        .flex-lg-#{$index} >* {
          flex: 0 0 calc(100% / #{$index});
          max-width: calc(100% / #{$index});
        }
    }
}

@media (min-width: 1300px) {
    @for $index from 1 through 12 {
        .flex-xl-#{$index} >* {
          flex: 0 0 calc(100% / #{$index});
          max-width: calc(100% / #{$index});
        }
    }
}

@for $i from 1 through 6 {
    .flex-gap-#{$i * 5} {
        margin: 0 -(#{$i * 2.5}px);
        >* {
          padding-left: #{$i * 2.5}px;
          padding-right: #{$i * 2.5}px;
        }
    }
}

@media (min-width: 750px) {
    @for $i from 1 through 6 {
        .flex-gap-md-#{$i * 5} {
            margin: 0 -(#{$i * 2.5}px);
            >* {
              padding-left: #{$i * 2.5}px;
              padding-right: #{$i * 2.5}px;
          }
      }
  }
}

.flex-gap {
    margin: 0 calc(-1 * (var(--flex-gap) / 2));
    >* {
        padding: 0 calc(var(--flex-gap) / 2);
    }
}
@media (min-width: 750px) {
    .flex-gap-md {
        margin: 0 calc(-1 * (var(--flex-gap-md) / 2));
        >* {
            padding: 0 calc(var(--flex-gap-md) / 2);
        }
    }
}
@media (min-width: 1000px) {
    .flex-gap-lg {
        margin: 0 calc(-1 * (var(--flex-gap-lg) / 2));
        >* {
            padding: 0 calc(var(--flex-gap-lg) / 2);
        }
    }
}
@media (min-width: 1300px) {
    .flex-gap-xl {
        margin: 0 calc(-1 * (var(--flex-gap-xl) / 2));
        >* {
            padding: 0 calc(var(--flex-gap-xl) / 2);
        }
    }
}