@-webkit-keyframes spinner {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spinner {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@-webkit-keyframes opacity {
  0% { -webkit-transform: translate(-50%, -50%) scale(1.08); opacity: 0; }
  25% { -webkit-transform: translate(-50%, -50%) scale(1.06); opacity: 0.25; }
  50% { -webkit-transform: translate(-50%, -50%) scale(1.04); opacity: 0.5; }
  75% { -webkit-transform: translate(-50%, -50%) scale(1.02); opacity: 0.75; }
  100% { -webkit-transform: translate(-50%, -50%) scale(1); opacity: 1; }
}

@keyframes opacity {
  0% { transform: translate(-50%, -50%) scale(1.08); opacity: 0; }
  25% { transform: translate(-50%, -50%) scale(1.06); opacity: 0.25; }
  50% { transform: translate(-50%, -50%) scale(1.04); opacity: 0.5; }
  75% { transform: translate(-50%, -50%) scale(1.02); opacity: 0.75; }
  100% { transform: translate(-50%, -50%) scale(1); opacity: 1; }
}



