  box-sizing: border-box;
  -webkit-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
  font-display: optional;
}

body { 
  line-height: 1.4;
  background: var(--background-color);
  text-rendering: optimizeLegibility;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing:antialiased;
  font-size: var(--body-fs);
  @media (min-width: 750px) {
    font-size: var(--body-fs-md);
  }
  @media (min-width: 1000px) {
    font-size: var(--body-fs-lg);
  }
  @media (min-width: 1300px) {
    font-size: var(--body-fs-xl);
  }
  // &:not(.overflow-hidden) {
  //   #main-content {
  //     contain: paint;
  //   }  
  // }
  #main-content {
    min-height: 60vh;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  &:before, &:after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
}
:focus {
  outline: none;
}
body, select, input, textarea {
  font-family: var(--body-font);
  font-weight: var(--body-fw);
  color: var(--text-color);
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

a {
  text-decoration: none;
  color: var(--text-color);
  &:focus {
    outline: none;
  }
}

p {
  margin: 20px 0;
}

ul, ol, dl {
  margin: 0;
}

ul, li, ol, summary {
  list-style: none;
}

ul {
  padding: 0;
  list-style: disc inside none;
}

ol {
  padding: 0;
  list-style: decimal inside none;
}
details > summary::-webkit-details-marker {
  display: none;
}
small {
  font-size: 80%;
}

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -.25rem;
}

sup {
  top: -.5rem;
}

label {
  display: inline-block;
  margin-bottom: 6px;
}

button {
  border: none;
  outline: none;
  background: none;
  font-family: var(--body-font);
  @extend %transition;
  * {
    pointer-events: none;
  }
  &:focus {
    -webkit-box-shadow: 0 0 0 #fff;
    box-shadow: 0 0 0 #fff;
  }
}

form {
  display: block;
  margin: 0;
}

select, input, textarea {
  background: none;
  appearance: none;
}

select:focus, input:focus, textarea:focus {
  outline: none;
}
select, input {
  width: 100%;
  border: 1px solid var(--miscellaneous-color);
}

select {
  max-width: 100%;
}

input, textarea {
  padding: 5px 15px;
}

input::-webkit-input-placeholder, textarea::-webkit-input-placeholder, input:-moz-placeholder, textarea:-moz-placeholder, input::-moz-placeholder, textarea::-moz-placeholder, input:-ms-input-placeholder, textarea:-ms-input-placeholder {
  color: #757575;
}

input {
  height: 40px;
}

textarea {
  width: 100%;
  height: 158px;
  border: 1px solid var(--miscellaneous-color);
}

