    @include ab(50%,50%,n,n);
    margin-top: -13px;
    @include margin-start(-13px);
    display: flex;
  }
}
.overflow-hidden {
  padding-right: var(--scrollbar-width); 
}
.open-mini-search {
  overflow-y: hidden;
  padding-right: var(--scrollbar-width);  
}
.spr-form, .spr-badge {
  .spr-icon {
    width: 13px;
    height: 13px;
    min-width: auto;
    min-height: auto;
    @include margin-end(3px);
    &:before {
      font-size: inherit;
      width: 13px;
      height: 13px;
    }
  }
  .spr-badge-starrating .spr-icon {
    font-size: 95%;
  }
  .spr-icon.spr-icon-star-empty:before {
    opacity: 1;
  }
  .spr-icon-star {
    &:before {
      opacity: 1;
    }
  }
}
