// popup
.popup-gl {
  .popup-layer { 
    @include fixed(0,0,0,0);
    background: rgba(0,0,0,0.5);
    opacity: 0;
    visibility: hidden;
    z-index: 99;
    z-index: 99;
    transition: 0.2s all ease-in-out;
  }
  .popup-inner {
    max-width: 92vw;
    width: 100%;
    opacity: 0;
    visibility: hidden;
    transition: 0.35s all ease-in-out;
    @include fixed(50%,50%,n,n);
    z-index: 9;
    padding: 20px;
    background: var(--background-color);
    border-radius: var(--border-radius);
    // animation: opacity 0.25s both;
    transform: translate(-50%,-50%) scale(1.08);
    max-height: 90vh;
    z-index: -5;
    overflow-y: auto;
  }
  &.active {
    .popup-inner {
      z-index: 99;
      transform: translate(-50%,-50%) scale(1);
    }
    .popup-inner, .popup-layer {
      visibility: visible;
      opacity: 1;
    }
  }
  .btn-popup-close {
    @include ab(0,n,n,0);
    margin: 0;
    padding: 10px;
    display: flex;
    z-index: 1;
    background: var(--primary-color);
    path {
      stroke: #fff;
    }
  }
  @media (min-width: 750px) {
   .popup-inner {
      max-width: 500px;
    }
  }
}
.cart-popup-error {
  .popup-title {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    svg {
      width: 20px;
      height: 20px;
      @include margin-end(8px);
    }
  }
}
.cart-noti-popup {
  &.with-recommend.cart-recommend-loaded {
    .popup-inner {
      min-height: 659px;
    }
  }
  @media (min-width: 750px) {
   .popup-inner {
    max-width: 610px;
  }
  &.with-recommend.cart-recommend-loaded {
    .popup-inner {
      min-height: 760px;
    }
  }
}
}
div.cart-sidebar {
  .cart-loading {
    @include ab(50%,50%,n,n);
    display: flex;
    @include margin-start(-13px);
    margin-top: -13px;
  }
  .popup-inner {
    top: 0;
    bottom: 0;
    max-height: none;
    border-radius: 0;
    @include right(-100%);
    @include left(auto);
    transform: translate(0,0);
    padding: 0;
    transition: 0.25s all ease-in-out;
    overflow: hidden; 
    padding-bottom: env(safe-area-inset-bottom);
    @media (min-width: 750px) {
      max-width: 460px;
    }
  }
  .popup-content {
    display: flex;
    flex-direction: column;
    height: 100%;
  }
  cart-items {
    height: 100%;
    overflow-y: auto;
  }
  .cart-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 20px;
    padding-top: 15px;
    margin: 0 20px;
    border-bottom: 1px solid var(--miscellaneous-color);
    h3 {
      font-size: 24px;
      font-weight: 500;
      a {
        transition: 0.2s all ease-in-out;
      }
      &:hover {
        a {
          color: var(--primary-color);
          text-decoration: underline;
        }
      }
    }
    .btn-popup-close {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: var(--border-radius);
      background: none;
      padding: 5px 17px;
      border: 1px solid var(--miscellaneous-color);
      height: 36px;
      font-weight: 700;
      font-size: 13px;
      color: var(--text-color);
      svg {
        width: 10px;
        height: 10px;
        @include margin-end(13px);
        path {
          stroke: var(--text-color);
        }
      }
    }
  }
  &.active {
    .popup-inner {
      @include right(0);
      transform: translate(0,0);
    }
  }
}
div.cart-popup {
  .cart-header {
    margin-bottom: 20px;
    .totals__subtotal-value {
      margin: 0;
      font-size: 20px;
      font-weight: 500;
      color: var(--primary-color);
    }
  }
  @media (min-width: 750px) {
    .popup-inner {
      max-width: 740px;
      min-height: 532px;
    }
  }
}
.section-static-newsletter-popup {
  @media (min-width: 750px) {
    .popup-inner {
      max-width: 720px;
      min-width: 720px;
    }
  }
}
// end popup

